import React from "react";
import { Modal, Button } from "react-bootstrap";
import { Box, Typography, Stack, Avatar, Divider } from "@mui/material";
import { genrateUrl } from "../../../utils/genrateUrl";
const ProfileModal = ({ show, handleClose, profiledata }) => {
  if (!profiledata) return null;
  const business = profiledata?.businessInfo || {};
  return (
    <Modal show={show} onHide={handleClose} centered size="md" className="custom-modal">
      <Modal.Header closeButton>
        <Modal.Title>Profile Details</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {/* Profile Header */}
        <Stack alignItems="center" spacing={1.2} mb={2}>
          <Avatar
            src={genrateUrl(profiledata?.profile)}
            sx={{
              width: 80,
              height: 80,
              border: "2px solid var(--bs-primary3)",
              boxShadow: "0 0 8px rgba(0,0,0,0.1)",
            }}
          />
          <Typography variant="h6" sx={{ fontWeight: 600, textAlign: "center" }}>
            {profiledata?.name || ""}
          </Typography>
          <Typography
            variant="body2"
            sx={{ color: "text.secondary", textAlign: "center" }}
          >
            Code: {profiledata?.refer_id || "N/A"}
          </Typography>
        </Stack>

        <Divider sx={{ mb: 2 }} />

        {/* Profile Details */}
        <Box sx={{ px: 1 }}>
          {profiledata?.isGstAvailable ? (
            <>
              <Typography variant="body2" sx={{ mb: 1 }}>
                <strong>GST Number:</strong> {business?.gst_NUmber || ""}
              </Typography>
              <Typography variant="body2" sx={{ mb: 1 }}>
                <strong>Legal Name:</strong>{" "}
                {business?.legal_name_of_business || ""}
              </Typography>
              <Typography variant="body2" sx={{ mb: 1 }}>
                <strong>Business Type:</strong>{" "}
                {business?.constitution_of_business || ""}
              </Typography>
              <Typography variant="body2">
                <strong>Address:</strong>{" "}
                {business?.principal_place_address || ""}
              </Typography>
            </>
          ) : (
            <>
              <Typography variant="body2" sx={{ mb: 1 }}>
                <strong>Shop:</strong> {profiledata?.shop_name || ""}
              </Typography>
              <Typography variant="body2" sx={{ mb: 1 }}>
                <strong>Address:</strong> {profiledata?.business_Address || ""}
              </Typography>
              <Typography variant="body2" sx={{ mb: 1 }}>
                <strong>Area:</strong> {profiledata?.business_Area || ""}
              </Typography>
              <Typography variant="body2">
                <strong>City:</strong> {profiledata?.business_city || ""}
              </Typography>
            </>
          )}
        </Box>
      </Modal.Body>

      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ProfileModal;
